import React from 'react';
import Card from 'react-bootstrap/Card';
import Button from "react-bootstrap/Button";

function CourseCards() {
    const dataFromDatabase = [
        {
            id: 1,
            title: "Joga",
            subtitle: "Anna Kowalska",
            text: "Zapraszamy na wyjtkow podr do harmonii ciaa i umysu z Ann Kowalsk, naszym dowiadczonym instruktorem jogi. Na naszych zajciach jogi Anna skupia si na stworzeniu przestrzeni dla spokoju i rwnowagi w Twoim yciu. ",
        },
        {
            id: 2,
            title: "Aerobik",
            subtitle: "Marek Nowak",
            text: " Popraw swoj wytrzymao, kondycj i samopoczucie, bawic si na penych energii treningach aerobikowych. Marek jest rdem inspiracji i motywacji - docz do nas i poczuj magi ruchu!",
        },
        // Dodaj wicej obiektw w zalenoci od potrzeb
    ];

    return (
        <div className="d-flex">
            {dataFromDatabase.map((data) => (
                <Card
                    key={data.id}
                    style={{width: '18rem', marginRight: '10px'}}
                >
                    <Card.Body>
                        <Card.Title>{data.title}</Card.Title>
                        <Card.Subtitle className="mb-2 text-muted">{data.subtitle}</Card.Subtitle>
                        <Card.Text>{data.text}</Card.Text>
                        <Button variant="dark">Zapisz si</Button>
                    </Card.Body>
                </Card>
            ))}
        </div>
    );
}

export default CourseCards;
